.chatItem {
    width: 50%;
    margin: 0 auto;
    display: flex;
    align-items: flex-end;
}

.avatar {
    margin-right: 20px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 1px solid #082103;
    object-fit: contain;
    z-index: 100;
}

.message {
    position: relative;
    padding: 5px 20px;
    border-radius: 8px;
    background: #082103;
}

.message:before {
    content:"";
    position:absolute;
    z-index:2;
    bottom:-3px;
    left:-7px;
    height:20px;
    border-left:20px solid #082103;
    border-bottom-right-radius: 16px 14px;
    -webkit-transform:translate(0, -2px);
 }

.message:after {
    content:"";
    position:absolute;
    z-index:3;
    bottom:-2px;
    left:4px;
    width:26px;
    height:20px;
    background:white;
    border-bottom-right-radius: 10px;
    -webkit-transform:translate(-30px, -2px);
 }


.name {
    color: #9193B5;

}

.messageInfo {
    display: flex;
}

.text {
    margin-right: 15px;
    color: #fff;
}

.time {
    margin-top: 8px;
    color: #929191;
}

